@import "../../assets/sundries";


.services {
  display: grid;
  grid-auto-flow: row;
  justify-content: center;
  align-content: start;
  justify-items: center;
  background-color: #ffffff;
  height: 57.8125rem;
}

.switch-buttons {
  display: grid;
  grid-auto-flow: column;
  justify-content: space-between;
  margin-top: 5.3125rem;
  width: 3.9375em;
}

.switch-button {
  height: 0.6875em;
  width: 0.6875em;
  border-radius: 50%;
  border: 2px solid #dfe0e1;
  cursor: pointer;
}

.switch-button--selected {
  border: 2px solid #1ca8dd;
}

.services__title {
  margin: 5.9375rem 0 0 0;
  font-family: "Gotham Black", sans-serif;
  color: #394b50;
  font-size: 1.875em;
}

.services__title-2 {
  margin: 2px 0 0 0;
  font-weight: normal;
  font-family: "Open Sans Regular", sans-serif;
  color:#46575c;
  font-size: 1em;
}

.services__services-items {
  width: 73.125rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-content: space-between;
}

.services-item {
  display: grid;
  justify-content: start;
  grid-auto-flow: column;
  margin-top: 4.6875rem;
}

.services-item.right {
  justify-content: end;
}

.services-item__info-wrapper {
  width: 26.875rem;
}

.services-item__title {
  margin: 0;
  font-family: "Lato", sans-serif;
  font-size: 1em;
  font-weight: bold;
  text-transform: uppercase;
  color: #394b50;
}

.services-item__text {
  margin-top: 0.625rem;
  font-family: "Open Sans Regular", sans-serif;
  font-size: 0.8125em;
  line-height: 1.6923em;
  color: #7b7b7b;
}

.services-item__img {
  width: 5.25rem;
  font-size: 2.6875em;
  color: #709dca;
}

.services-item__img svg:hover {
  color: #f6772c;
}