    grid-template-columns: 0.1fr 1fr ;
  }
  .top-nav {
    width: 64rem ;
  }
  .switch-buttons {
    margin-top: 9% ;
  }
  .services {
    height: 68rem ;
  }
  .about {
    height: 42rem ;
  }
  .plan__accounts, .plan__support, .plan__cloud {
    width: 140% ;
  }
  .bubble {
    height: 20rem ;
    width: 19.5rem ;
  }
